@font-face {
	font-family: 'icomoon';
	src: url('fonts/icomoon.eot');
}
@font-face {
	font-family: 'icomoon';
	src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'),
		 url(data:application/font-woff;charset=utf-8;base64,) format('woff');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	font-family: 'icomoon';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-search:before {
	content: "\e1ba";
}
.icon-mail:before {
	content: "\e491";
}
.icon-twitter:before {
	content: "\e49f";
}
.icon-feed:before {
	content: "\e4a2";
}
.icon-github:before {
	content: "\e4ba";
}
